a,
.control {
  color: #54578c;
}
.bfloat > svg {
  fill: #54578c;
}
.bmodal hr {
  border-top: 1px solid #54578c;
}
#banner {
  background: rgba(217, 240, 214, 0.7);
  border-bottom: 1px solid #b9d9b7;
}
b {
  color: #1a8522;
}
b.admin {
  color: #d60000;
}
b.moderator {
  color: #7800b3;
}
blockquote a {
  color: #54578c;
}
a:hover {
  color: #d17c43;
}
article,
aside,
.pagination {
  background-color: #d9f0d6;
  border-right: 1px solid #b9d9b7;
  border-bottom: 1px solid #b9d9b7;
}
article.highlight {
  background-color: #b9ccb4;
  border-color: #9fbf9d;
}
body {
  color: #0a0a0a;
  background-color: #f4ffee;
  font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
  font-size: 10pt;
}
pre,
.themed {
  color: #59543f;
  font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
  font-size: 10pt;
}
em {
  color: #6e9e06;
}
h1 {
  color: #b32407;
  font-family: "MS Pゴシック", "MS PGothic", "Mona", "Hiragino Kaku Gothic Pro", Helvetica, sans-serif;
  font-size: 30px;
  text-shadow: #8e3523 1px 1px 0px;
  letter-spacing: 1px;
}
h2 {
  color: #b32407;
  font-family: "MS Pゴシック", "MS PGothic", "Mona", "Hiragino Kaku Gothic Pro", Helvetica, sans-serif;
  text-shadow: #8e3523 1px 1px 0px;
}
h3 {
  color: #2f4d0c;
}
header nav a {
  color: #0a0a0a;
}
hr {
  border-top: 1px solid #bac9b7;
}
.popup-menu li:hover {
  background-color: #f9fcf7;
}
.email {
  color: #54578c;
}
.modal,
.bmodal,
.preview,
.popup-menu {
  background-color: #f6faf3;
  border-right: 1px solid #e5e8e2;
  border-bottom: 1px solid #e5e8e2;
}
.option_tab_sel a.tab_sel {
  color: #BA8D7A;
}
article.editing {
  background-color: #f0e5ba;
  border-color: #dbd1aa;
}
.omit {
  color: #73806c;
}
